<template>
	<div class="app-content" v-if="!!Object.keys(content).length">
		<div class="container">
			<div class="page-title">
				<h1>Контакты</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
                <fieldset>
                    <div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок страницы</label>
                            <InputLangGroup v-model="content.pageTitle" :content="content.pageTitle"/>
                        </div>
                    </div>
                </fieldset>
				<fieldset>
                    <legend>Список контактов</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.contactsTitle" :content="content.contactsTitle"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Телефон 1</label>
                            <div class="input-group">
                                <input type="text" class="form-control" v-model="content.phone1.value">
                            </div>
                            <InputLangGroup v-model="content.phone1.description" :content="content.phone1.description"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Телефон 2</label>
                            <div class="input-group">
                                <input type="text" class="form-control" v-model="content.phone2.value">
                            </div>
                            <InputLangGroup v-model="content.phone2.description" :content="content.phone2.description"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Email</label>
                            <div class="input-group">
                                <input type="text" class="form-control" v-model="content.email.value">
                            </div>
                            <InputLangGroup v-model="content.email.description" :content="content.email.description"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Время работы</label>
                            <InputLangGroup v-model="content.workTime.value" :content="content.workTime.value"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Текст описание</label>
                            <InputLangGroup v-model="content.text" :content="content.text" :isTextarea="true"/>
                        </div>
					</div>
				</fieldset>
				<fieldset>
					<legend>Форма обратной связи</legend>
					<div class="row">
						<div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.formTitle" :content="content.formTitle"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Имя"</label>
                            <InputLangGroup v-model="content.formFields.name" :content="content.formFields.name"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Email"</label>
                            <InputLangGroup v-model="content.formFields.email" :content="content.formFields.email"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Поле "Сообщение"</label>
                            <InputLangGroup v-model="content.formFields.msg" :content="content.formFields.msg"/>
                        </div><div class="col-12">
                            <label class="form-label">Кнопка формы</label>
                            <InputLangGroup v-model="content.formBtnText" :content="content.formBtnText"/>
                        </div>
					</div>
				</fieldset>
				<fieldset>
					<legend>Карта сайта</legend>
					<div class="row">
						<div class="col-12">
							<label class="form-label">Ссылка на карту</label>
							<div class="input-group">
								<input type="text" class="form-control" placeholder="Введите текст" v-model="content.mapURL">
							</div>
						</div>
					</div>
				</fieldset>
			</div>
		</div>
	</div>
</template>

<script>
import InputLangGroup from './../components/InputLangGroup.vue';

export default {
    data() {
        return {
            content: '',
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    components: {
        InputLangGroup
    },
    computed: {
        pageContent() {
            return this.$store.getters['getContactspageContent'];
        }
    },
    watch: {
        pageContent(value) {
            this.content = value;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveContactsPage', this.content)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        }
    },
    created() {
        this.content = this.pageContent;
    },
    mounted() {

    }
};
</script>